import { Container } from "./styles";
import SupportAgentIcon from '@mui/icons-material/SupportAgent';
import BalanceIcon from '@mui/icons-material/Balance';
import GroupsIcon from '@mui/icons-material/Groups';

const Services = () => {
    return(
        <Container>
            <div className="boxes">
                <div className="box">
                    <div className="items">
                        <div className="img">
                            <SupportAgentIcon />
                        </div>
                        <div className="text">Marque sua
                            <h3>consulta conosco</h3>
                        </div>
                    </div>
                </div>
                <div className="box">
                    <div className="items">
                        <div className="img">
                            <BalanceIcon />
                        </div>
                        <div className="text">Tire suas
                            <h3>dvidas aqui</h3>
                        </div>
                    </div>
                </div>
                <div className="box">
                    <div className="items">
                        <div className="img">
                            <GroupsIcon />
                        </div>
                        <div className="text">Junte-se
                            <h3>ao nosso time</h3>
                        </div>
                    </div>
                </div>
            </div>
        </Container>
    );
}

export default Services;