import { Container } from "./styles";
import { Pagination, Autoplay } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/autoplay';

const AVTR1 = require("../../assets/people.jpg") as string;
const AVTR2 = require("../../assets/people.jpg") as string;

const data = [
    {
        avatar: AVTR1,
        name: 'Lutaremos por justia no seu caso sem deixar nenhuma ponta solta atrapalhar',
        review: 'Empenho total em seu caso',
        review2: 'O melhor resultado possvel'
    },
    {
        avatar: AVTR2,
        name: 'Lutaremos por justia no seu caso sem deixar nenhuma ponta solta atrapalhar',
        review: 'Empenho total em seu caso',
        review2: 'O melhor resultado possvel'
    }
]

const Slide = () => {
    
    return(
        <Container>
        
            <div className="testimonial2">

                <Swiper style={{width: '100%'}} className='container testimonials__container'
                    // install Swiper modules
                    modules={[Pagination, Autoplay]}
                    spaceBetween={40}
                    slidesPerView={1}
                    pagination={{ clickable: true }}
                    autoplay={{delay: 7000}}
                    >
                        {
                            data.map(({avatar, name, review, review2}, index) => {
                                return (
                                    <SwiperSlide key={index} className="testimonial">
                                        <div className="client__avatar">
                                            <img src={avatar} />
                                            <div className="text">
                                                <small className='client__review1'>{review}</small>
                                                <h5 className='client__name'>{name}</h5>
                                                <small className='client__review2'>{review2}</small>
                                            </div>
                                        </div>
                                    </SwiperSlide>
                                )
                            })
                        }
                    </Swiper>
            </div>
        </Container>
    );
}

export default Slide;