import styled from "styled-components";

export const Container = styled.div`
    padding: 100px 15px;
    margin-inline: auto;
    color: #667;
    max-width: 490px;
    
    .aboutTop {
        .wrapper {
            width: 100%;
            background-color: #c0b596;
            padding-bottom: 10px;
            padding-right: 8px;
        }

        img {
            width: 100%;
            margin-top: -20px;
            margin-left: -8px;
        }
    }  

    .aboutBottom {
        display: flex;
        flex-direction: column;
        padding-top: 40px;

        img {
            width: 169px;
            height: 58px;
        }
    }

    @media screen and (min-width: 990px) {
        max-width: 1000px;
        display: grid;
        grid-template-columns: 55% 42%;
        gap: 3%;

        .aboutTop {
            .wrapper {
                padding-bottom: 10px;
                padding-right: 0px;
            }
            
            img {
                margin-top: 20px;
                margin-left: 15px;
            }
        }

        .aboutBottom {
            margin-top: -23px;
            padding-top: 0;
        }
    }
`;