import styled from "styled-components";

export const Container = styled.div`
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #272c3f;
    color: #e0e0e0;
    font-size: 16px;
    z-index: 999;

    ul {
        list-style-type: none;
        padding: 0;
    }
    
    .headerTop {
        padding-inline: 20px;
        display: flex;
        flex-direction: column;
    }

    .adress {
        display: flex;
        align-items: center;

        ul {
            width: 100%;
            display: flex;
            justify-content: center;
        }

        li {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 5px;
        }
    }

    .headerContact {
        display: flex;
        flex-direction: column;
        align-items: center;

        p {
            color: #fff;
            border: 3px solid #c0b596;
            padding: 13px;
            border-radius: 50px;
            cursor: pointer;
            transition: ease-in-out .3s;

            :hover {
                background-color: #1c6077;
            }
        }

        li {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 5px;
        }
    }

    .barra {
        width: 1px;
        background-color: #1c6077;
        margin-inline: 13px;
    }

    .headerContact ul {
        display: flex;
        justify-content: space-evenly;
    }

    .headerBottom {
        padding-inline: 20px;
    }

    .row {
        padding-block: 30px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-top: 1px solid #1c6077;
    }

    .logo {
        cursor: pointer;
        font-weight: bold;
        font-size: 19px;
    }

    .open {
        cursor: pointer;
        width: 35px;
        height: 35px;

        .icon {
            height: 100%;
            width: 100%;
        }
    }

    .menuWeb {
        display: none;
    }

    .menuPhone {
        width: 100%;
        overflow: hidden;
        transition: ease-in-out .4s;
        border-top: 1px solid #1c6077;

        ul {
            display: flex;
            flex-direction: column;
            gap: 17px;
            height: auto;
            padding-block: 20px;

            li:hover {
                color: #c0b596;
                cursor: pointer;
            }
        }
    }

    @media screen and (min-width: 720px) {
        position: initial;
        top: initial;

        .headerContact {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            border-bottom: 1px solid #1c6077;
        }

        .row {
            border-top: none;

            .logo {
                width: 200px;
                font-size: 22px;
            }

            .open {
                display: none;
            }
        }

        .menuPhone {
            display: none;
        }

        .menuWeb {
            display: block;
            border-top: none;
            align-items: center;

            ul {
                display: flex;
                flex-direction: row;
                justify-content: end;
                gap: 18px;

                li:hover {
                    color: #c0b596;
                    cursor: pointer;
                }
            }
        }

        .headerBottomMainWrapper {
            display: flex;
            justify-content: space-between;
            align-items: center; 
            height: 100px;
        }
    }

    @media screen and (min-width: 900px) {

        .headerTop {
            max-width: 1110px;
            margin-inline: auto;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            border-bottom: 1px solid #1c6077;
        }

        .headerBottomMainWrapper {
            max-width: 1110px;
            margin-inline: auto;
        }

        .headerContact {
            width: 60%;
            border: none;
        }
    }
    
`;