import styled from "styled-components";

export const Container = styled.div`
    margin-top: 290px;
    width: 100%;
    margin-inline: auto;
    color: #373737;

    .testimonial {
        text-align: center;
        user-select: none;
        height: 290px;
    }

    .testimonial1 {
        display: flex;
        justify-content: center;

        img {
            max-width: 320px;
        }
    }

    .testimonial2 {
        background-color: #f1f1f1;
        display: flex;
        flex-direction: column;
        align-items: center;
        box-shadow: 0 0 14px 0 #00000080;

        p {
            margin: 30px 0 0 0 ;
            color: #777777;
        }

        h3 {
            margin: 5px 0 0 0 ;
            font-size: 23px;
        }
    }
    
    .swiper-wrapper {
        max-width: 400px;
    }

    .client__avatar {
        overflow: hidden;
        height: 100%;

        img {
            height: 100%;
        }
    }

    .text {
        margin-top: -220px;
    }

    .client__review1, .client__review2 {
        font-weight: 300;
        font-size: 15px;
        text-align: left;
        display: block;
        max-width: 320px;
        margin-left: 5%;
        color: #f1f1f1;
    }

    .client__name {
        font-size: 23px;
        margin-left: 5%;
        margin-block: 20px;
        width: 320px;
        color: #f1f1f1;
        text-align: left;
    }

    @media screen and (min-width: 720px) {
        margin-top: 0;
        padding: 0;

        .text {
            margin-top: -280px;
        }

        .testimonial {
            height: 450px;
        }
    }

    @media screen and (min-width: 900px) {
        .text {
            margin-top: -330px;
        }

        .client__review1, .client__review2 {
            font-size: 22px;
            max-width: 360px;
        }

        .client__name {
            font-size: 30px;
            width: 360px;
        }

        .testimonial2 {
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    @media screen and (min-width: 1220px) {
        .testimonial {
            height: 520px;
        }
    }

    @media screen and (min-width: 1430px) {
        .testimonial {
            height: 550px;
        }

        .text {
            margin-left: 140px;
        }
    }

    @media screen and (min-width: 1515px) {
        .testimonial {
            height: 655px;
            width: 100%;
        }

        .text {
            margin-left: 250px;
        }
    }

    @media screen and (min-width: 1810px) {
        .client__avatar {
            img {
                width: 100%;
                height: auto;
            }
        }

        .text {
            margin-top: -500px;
            margin-left: 470px;
        }
    }

    @media screen and (min-width: 2300px) {
        .text {
            margin-top: -700px;
            margin-left: 770px;
        }
    }

    @media screen and (min-width: 2900px) {
        .text {
            margin-top: -900px;
            margin-left: 1100px;
        }
    }

    @media screen and (min-width: 3500px) {
        .text {
            margin-top: -1200px;
            margin-left: 1400px;
        }
    }

    @media screen and (min-width: 4300px) {
        .text {
            margin-top: -1400px;
            margin-left: 1550px;
        }
    }
`;