import { useState } from "react";
import { Container } from "./styles";

import AddLocationAltIcon from '@mui/icons-material/AddLocationAlt';
import LocalPhoneIcon from '@mui/icons-material/LocalPhone';
import QueryBuilderIcon from '@mui/icons-material/QueryBuilder';
import MenuIcon from '@mui/icons-material/Menu';

const Header = () => {
    const [ menu, setMenu ] = useState(false);

    const wrapper = () => {
        if(menu === true) {
            setMenu(false);
        } else {
            setMenu(true);
        }
    }
    
    return (
        <Container>
            <div className="headerTop">
                <div className="adress">
                    <ul>
                        <li>
                            <AddLocationAltIcon />
                            Rua Exemplo, 123, So Paulo - SP
                        </li>
                    </ul>
                </div>
                <div className="headerContact" id="headerContact">
                    <ul>
                        <li>
                            <LocalPhoneIcon />
                            (99)9999-9999
                        </li>
                        <div className="barra" />
                        <li>
                            <QueryBuilderIcon />
                            09h00 - 17h00
                        </li>
                    </ul>
                    <p>Fale conosco</p>
                </div>
            </div>
            <div className="headerBottom">
                <div className="container">
                    <div className="headerBottomMainWrapper">
                        <div className="row">
                            <div className="logo">
                                LOGO EXEMPLO
                            </div>
                            <div 
                                onClick={wrapper}
                                className="open"
                            >
                                <MenuIcon 
                                    className="icon" 
                                />
                            </div>
                        </div>
                        <div 
                            className="menuPhone" 
                            style={{
                                height: menu ? '240px' : '0px'
                            }}
                        >
                            <ul >
                                <li>Primeiro</li>
                                <li>Segundo</li>
                                <li>Terceiro</li>
                                <li>Quarto</li>
                                <li>Quinto</li>
                            </ul>
                        </div>
                        <div className="menuWeb">
                            <ul >
                                <li>Primeiro</li>
                                <li>Segundo</li>
                                <li>Terceiro</li>
                                <li>Quarto</li>
                                <li>Quinto</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </Container>
    );
}

export default Header;