import styled from "styled-components";

export const Container = styled.div`
    padding: 30px 15px;
    text-align: center;

    h2 {
        font-size: 25px;
    }

    h3 {
        font-size: 40px;
        margin-block: 20px;
    }

    p {
        font-size: 18px;
        margin-top: 0;
    }
    
    .boxes {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 10px;
        max-width: 450px;
        margin: 50px auto;
    }

    .box {
        background-color: #f1f1f1;
        text-align: center;
    }

    @media screen and (min-width: 1030px) {
        .boxes {
            grid-template-columns: 1fr 1fr 1fr 1fr;
            max-width: 900px;
        }
    }
`;