import { Container } from "./styles";
import PhotoPNG from "../../assets/photoPNG.png";
import { Pagination, Autoplay } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/autoplay';

const AVTR1 = require("../../assets/photoPNG.png") as string;
const AVTR2 = require("../../assets/photoPNG.png") as string;

const data = [
    {
        avatar: AVTR1,
        name: 'Pietro Gomes - CEO Iternational Business',
        review: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Aliquam unde repellat est, optio placeat sed dolorem ea hic totam quos modi dolores rerum ipsa facere cum perspiciatis tempore nesciunt corrupti.'
    },
    {
        avatar: AVTR2,
        name: 'Gomes Pietro - CEO International Comunication',
        review: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Aliquam unde repellat est, optio placeat sed dolorem ea hic totam quos modi dolores rerum ipsa facere cum perspiciatis tempore nesciunt corrupti.'
    }
]

const Testmonial = () => {
    
    return(
        <Container>
            <div className="testimonial1">
                <img src={PhotoPNG} alt="Advogado segunda foto" />
            </div>
            <div className="testimonial2">
        
                <p>O que nossos clientes dizem</p>
                <h3>Testemunhos de clientes</h3>
            
                <Swiper style={{width: '100%'}} className='container testimonials__container'
                    // install Swiper modules
                    modules={[Pagination, Autoplay]}
                    spaceBetween={40}
                    slidesPerView={1}
                    pagination={{ clickable: true }}
                    autoplay={{delay: 10000}}
                    >
                        {
                            data.map(({avatar, name, review}, index) => {
                                return (
                                    <SwiperSlide key={index} className="testimonial">
                                        <div className="client__avatar">
                                            <img src={avatar} />
                                        </div>
                                        <h5 className='client__name'>{name}</h5>
                                        <small className='client__review'>
                                            {review}
                                        </small>
                                    </SwiperSlide>
                                )
                            })
                        }
                    </Swiper>
            </div>
        </Container>
    );
}

export default Testmonial;