import styled from "styled-components";

export const Container = styled.div`
    max-width: 500px;
    margin-inline: auto;
    padding: 100px 15px;
    color: #373737;

    .testimonial1 {
        display: flex;
        justify-content: center;

        img {
            max-width: 320px;
        }
    }

    .testimonial2 {
        background-color: #f1f1f1;
        border-radius: 30px;
        display: flex;
        flex-direction: column;
        align-items: center;
        box-shadow: 0 0 14px 0 #00000080;

        p {
            margin: 30px 0 0 0 ;
            color: #777777;
        }

        h3 {
            margin: 5px 0 0 0 ;
            font-size: 23px;
        }
    }
    
    .swiper-wrapper {
        max-width: 400px;
    }

    .client__avatar {
        width: 7rem;
        aspect-ratio: 1/1;
        overflow: hidden;
        border-radius: 50%;
        margin: 0 auto 1rem;
        border: 2px solid #c0b596;

        img {
            width: 100%;
        }
    }

    .testimonial {
        text-align: center;
        padding-block: 50px;
        user-select: none;
        height: 290px;
    }

    .client__name {
        font-size: 15px;
        margin-top: 15px;
        margin-inline: auto;
        width: 300px;
    }

    .client__review {
        font-weight: 300;
        font-size: 16px;
        display: block;
        max-width: 500px;
        margin: 14px auto;
        padding-inline: 20px;
    }

    @media screen and (min-width: 900px) {
        max-width: 1000px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        padding: 100px 15px 0 15px;

        .testimonial {
            height: 300px;
            padding-block: 20px;
        }

        .testimonial1 {
            img {
                height: 500px;
            }
        }

        .testimonial2 {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 50px;
        }
    }
`;