import styled from "styled-components";

export const Container = styled.div`
    background-color: #f1f1f1;
    padding: 50px 15px;
    color: #777;

    .boxes {
        max-width: 1140px;
        padding: 50px 0 20px 0;
        display: grid;
        grid-template-columns: 1fr;
        gap: 40px;
    }

    .box {
        height: 90px;
        margin-inline: auto;

        .items {
            max-width: 420px;
            display: grid;
            grid-template-columns: 25% 72%;
            gap: 3%;

            .img {
                border: 1px solid #c0b596;
                border-radius: 50%;
                height: 75px;
                width: 75px;
                display: flex;
                justify-content: center;
                align-items: center;

                svg {
                    width: 60%;
                    height: 60%;
                    color: #c0b596;
                }
            }
    
            .text {
                display: flex;
                flex-direction: column;
                justify-content: center;
                gap: 10px;

                h3 {
                    color: #373737;
                    margin: 0;
                    font-size: 22px;
                    border-bottom: 1px solid #c0b596;
                }

                p {
                    margin: 0;
                }
            }
        }
    }

    @media screen and (min-width: 900px) {
        .boxes {
            grid-template-columns: 1fr 1fr;
            margin-inline: auto;
        }

    }

    @media screen and (min-width: 1160px) {
        .boxes {
            grid-template-columns: 1fr 1fr 1fr;
            max-width: 1200px;
        }
    }

    @media screen and (min-width: 1300px) {
        .box {
            width: 380px;
        }
    }
`;